    .wheel-animate {
      /* animation: name duration timing-function delay iteration-count direction fill-mode; */
      animation: wheelSpinAnimate 6s infinite forwards;
    }
    @-webkit-keyframes wheelSpinAnimate {
      0%   { transform: rotateZ(0deg); border-color:#eccb11;}
      50%  { transform: rotateZ(180deg);  border-color:#0837b8;}
      100% { transform: rotateZ(360deg); border-color:#76aa16; }
    }
    @keyframes wheelSpinAnimate {
      0%   { transform: rotateZ(0deg); border-color:#eccb11;}
      50%  { transform: rotateZ(180deg); border-color:#0837b8; }
      100% { transform: rotateZ(360deg); border-color:#76aa16; }
